<template>
  <div class="mdui-row">
    <editor class="line-20" mode="htmlmixed" :value="code" @input="acUpdateCode" />
    <div class="mdui-m-t-1">
      <mdui-btn @click="format" dense ripple></mdui-btn>
      <mdui-btn mdui-tooltip="{content: ''}" @click="compress(0)" dense ripple></mdui-btn>
      <mdui-btn
        mdui-tooltip="{content: ' rgb '}"
        @click="compress(1)"
        dense
        ripple
      >
        
      </mdui-btn>
      <mdui-btn
        mdui-tooltip="{content: ''}"
        @click="compress(2)"
        dense
        ripple
        >
      </mdui-btn>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import CleanCss from 'clean-css'

export default {
  name: 'Css',
  computed: {
    ...mapState('format/css', ['code'])
  },
  methods: {
    ...mapActions('format/css', ['acUpdateCode']),
    // 
    format() {
      const code = new CleanCss({ format: 'beautify' }).minify(this.code).styles
      this.acUpdateCode(code)
    },
    // 
    compress(level) {
      const code = new CleanCss({ level }).minify(this.code).styles
      this.acUpdateCode(code)
    }
  }
}
</script>
